/* ================================================================
   HOOK  useWindowSize
   ================================================================ */
function useWindowSize() {
  const [width, setWidth] = useState(
    typeof window !== "undefined" ? window.innerWidth : 1200
  );
  useEffect(() => {
    const onResize = () => setWidth(window.innerWidth);
    window.addEventListener("resize", onResize);
    return () => window.removeEventListener("resize", onResize);
  }, []);
  return width;
}

/* ================================================================
   DATA GENERATORS
   ================================================================ */
const DISTRICTS = [
  "","","","","","","",
  "","","","","","","",""
];

const BASE_PRICE = {
  "":24,"":21,"":18,"":14,"":17,
  "":13,"":12,"":11,"":13,"":7,
  "":9,"":10,"":7,"":12,"":13
};

const BASE_SUMMARY = {
  "":26,"":23,"":19,"":15,"":18,
  "":14,"":13,"":12,"":14,"":8,
  "":10,"":11,"":8,"":13,"":14
};

const generateTransactionData = () => {
  const data = [];
  for (let y = 2020; y <= 2025; y++) {
    for (let m = 1; m <= 12; m++) {
      DISTRICTS.forEach(d => {
        const base = BASE_PRICE[d];
        const trend = (y - 2020) * 0.8 + Math.sin((m + y * 12) * 0.3) * 1.5;
        const noise = (Math.random() - 0.5) * 2;
        data.push({
          district: d, year: y, month: m,
          date: `${y}-${String(m).padStart(2, "0")}`,
          price: Math.round((base + trend + noise) * 1000) / 1000,
          volume: Math.floor(Math.random() * 800 + 200),
          area: Math.floor(Math.random() * 50 + 59),
        });
      });
    }
  }
  return data;
};

const MONTHLY_TREND = (() => {
  const arr = [];
  for (let y = 2020; y <= 2025; y++)
    for (let m = 1; m <= 12; m++) {
      const base = 15 + (y - 2020) * 1.2 + Math.sin((m + y * 12) * 0.25) * 1.8;
      arr.push({
        date: `${y}.${String(m).padStart(2, "0")}`,
        price: Math.round(base * 100) / 100,
        volume: Math.floor(Math.random() * 5000 + 3000),
      });
    }
  return arr;
})();

const DISTRICT_SUMMARY = DISTRICTS.map(d => {
  const p = BASE_SUMMARY[d];
  const change = parseFloat((Math.random() * 8 - 2).toFixed(1));
  return {
    name: d, avgPrice: p, change,
    volume: Math.floor(Math.random() * 3000 + 500),
    avgArea: Math.floor(Math.random() * 30 + 60),
  };
});

