/* ================================================================
   DASHBOARD PAGE
   ================================================================ */
function DashboardPage() {
  const [sel, setSel] = useState("");
  const [period, setPeriod] = useState("1Y");
  const mob = useWindowSize() < 768;
  const allData = useMemo(generateTransactionData, []);

  const trend = useMemo(() => {
    const dd = allData.filter(d => d.district === sel);
    const mm = {};
    dd.forEach(d => {
      if (!mm[d.date]) mm[d.date] = { prices: [], vol: 0 };
      mm[d.date].prices.push(d.price); mm[d.date].vol += d.volume;
    });
    let arr = Object.entries(mm).map(([date, v]) => ({
      date,
      price: Math.round(v.prices.reduce((a, b) => a + b, 0) / v.prices.length * 100) / 100,
      volume: v.vol,
    })).sort((a, b) => a.date.localeCompare(b.date));
    const sl = { "6M": 6, "1Y": 12, "3Y": 36, ALL: arr.length };
    return arr.slice(-(sl[period] || 12));
  }, [sel, period, allData]);

  const info = DISTRICT_SUMMARY.find(d => d.name === sel);

  return (
    <div style={{ paddingTop: 70, minHeight: "100vh" }}>
      <div style={{ maxWidth: 1200, margin: "0 auto", padding: mob ? "24px 16px" : "40px 24px" }}>
        <div className="ani" style={{ marginBottom: 24 }}>
          <h1 style={{ fontSize: mob ? 22 : 28, fontWeight: 800, marginBottom: 6 }}> </h1>
          <p style={{ color: C.darkText, fontSize: mob ? 13 : 15 }}>   </p>
        </div>

        {/* KPI */}
        <div style={{ display: "grid", gridTemplateColumns: mob ? "1fr 1fr" : "repeat(4,1fr)", gap: mob ? 10 : 16, marginBottom: 24 }}>
          <KPICard title=" " value={info?.avgPrice || 0} unit="" change={info?.change} icon={DollarSign} color={C.primary} delay={1} />
          <KPICard title="" value={info?.volume.toLocaleString() || 0} unit="" change={2.3} icon={BarChart3} color={C.accent1} delay={2} />
          <KPICard title=" " value={info?.avgArea || 0} unit="" icon={Home} color={C.secondary} delay={3} />
          <KPICard title="" value="68.5" unit="%" change={-1.2} icon={Percent} color={C.accent2} delay={4} />
        </div>

        {/* Filters */}
        <div className="ani d2" style={{ display: "flex", gap: 10, marginBottom: 20, flexWrap: "wrap" }}>
          <select value={sel} onChange={e => setSel(e.target.value)} style={{ background: C.darkCard, border: `1px solid ${C.darkBorder}`, color: "#fff", padding: "10px 16px", borderRadius: 10, fontSize: 14, fontFamily: "'Noto Sans KR',sans-serif", cursor: "pointer", outline: "none", flex: mob ? 1 : "none" }}>
            {DISTRICTS.map(d => <option key={d} value={d}>{d}</option>)}
          </select>
          <div style={{ display: "flex", gap: 4, background: C.darkCard, borderRadius: 10, padding: 4, border: `1px solid ${C.darkBorder}` }}>
            {["6M","1Y","3Y","ALL"].map(p => (
              <button key={p} onClick={() => setPeriod(p)} style={{ background: period === p ? C.primary : "transparent", border: "none", color: period === p ? "#fff" : C.darkText, padding: "6px 12px", borderRadius: 8, fontSize: 13, fontWeight: 600, cursor: "pointer", fontFamily: "'Outfit',sans-serif" }}>{p}</button>
            ))}
          </div>
        </div>

        {/* Charts  stack on mobile */}
        <div style={{ display: "grid", gridTemplateColumns: mob ? "1fr" : "1fr 1fr", gap: mob ? 12 : 20, marginBottom: 24 }}>
          <div className="ani d3" style={{ background: C.darkCard, border: `1px solid ${C.darkBorder}`, borderRadius: 16, padding: mob ? "20px 8px" : 24 }}>
            <h3 style={{ fontSize: mob ? 14 : 16, fontWeight: 700, marginBottom: 16, paddingLeft: mob ? 8 : 0 }}>{sel}  </h3>
            <ResponsiveContainer width="100%" height={mob ? 220 : 280}>
              <AreaChart data={trend}>
                <defs><linearGradient id="dg" x1="0" y1="0" x2="0" y2="1"><stop offset="5%" stopColor={C.primary} stopOpacity={.25} /><stop offset="95%" stopColor={C.primary} stopOpacity={0} /></linearGradient></defs>
                <CartesianGrid strokeDasharray="3 3" stroke="rgba(255,255,255,.04)" />
                <XAxis dataKey="date" tick={{ fill: C.darkText, fontSize: 10 }} axisLine={false} tickLine={false} interval={mob ? 2 : 0} />
                <YAxis tick={{ fill: C.darkText, fontSize: 10 }} axisLine={false} tickLine={false} tickFormatter={v => `${v}`} width={35} />
                <Tooltip contentStyle={ttStyle} formatter={v => [`${v}`, ""]} />
                <Area type="monotone" dataKey="price" stroke={C.primary} strokeWidth={2} fill="url(#dg)" dot={false} />
              </AreaChart>
            </ResponsiveContainer>
          </div>
          <div className="ani d4" style={{ background: C.darkCard, border: `1px solid ${C.darkBorder}`, borderRadius: 16, padding: mob ? "20px 8px" : 24 }}>
            <h3 style={{ fontSize: mob ? 14 : 16, fontWeight: 700, marginBottom: 16, paddingLeft: mob ? 8 : 0 }}> </h3>
            <ResponsiveContainer width="100%" height={mob ? 220 : 280}>
              <BarChart data={trend}>
                <CartesianGrid strokeDasharray="3 3" stroke="rgba(255,255,255,.04)" />
                <XAxis dataKey="date" tick={{ fill: C.darkText, fontSize: 10 }} axisLine={false} tickLine={false} interval={mob ? 2 : 0} />
                <YAxis tick={{ fill: C.darkText, fontSize: 10 }} axisLine={false} tickLine={false} width={35} />
                <Tooltip contentStyle={ttStyle} formatter={v => [`${v}`, ""]} />
                <Bar dataKey="volume" fill={C.accent1} radius={[4, 4, 0, 0]} />
              </BarChart>
            </ResponsiveContainer>
          </div>
        </div>

        {/* Ranking table */}
        <div className="ani d5" style={{ background: C.darkCard, border: `1px solid ${C.darkBorder}`, borderRadius: 16, padding: mob ? 16 : 24 }}>
          <h3 style={{ fontSize: mob ? 14 : 16, fontWeight: 700, marginBottom: 16 }}>   </h3>
          <div style={{ overflowX: "auto", WebkitOverflowScrolling: "touch" }}>
            <table style={{ width: "100%", borderCollapse: "collapse", minWidth: mob ? 500 : "auto" }}>
              <thead><tr>{["",""," ","",""," "].map(h => <th key={h} style={{ padding: "10px 10px", textAlign: "left", fontSize: 11, fontWeight: 600, color: C.darkText, borderBottom: `1px solid ${C.darkBorder}`, whiteSpace: "nowrap" }}>{h}</th>)}</tr></thead>
              <tbody>
                {[...DISTRICT_SUMMARY].sort((a, b) => b.avgPrice - a.avgPrice).map((d, i) => (
                  <tr key={d.name} style={{ cursor: "pointer" }} onClick={() => setSel(d.name)}>
                    <td style={{ padding: "12px 10px", fontSize: 13, fontWeight: 700, color: i < 3 ? C.primary : C.darkTextLight }}>{i + 1}</td>
                    <td style={{ padding: "12px 10px", fontSize: 13, fontWeight: 600 }}>{d.name}</td>
                    <td style={{ padding: "12px 10px", fontSize: 13, fontFamily: "'Outfit',sans-serif", fontWeight: 600 }}>{d.avgPrice}</td>
                    <td style={{ padding: "12px 10px", fontSize: 12, fontWeight: 600, color: d.change >= 0 ? C.danger : C.secondary }}>{d.change >= 0 ? "+" : ""}{d.change}%</td>
                    <td style={{ padding: "12px 10px", fontSize: 13, color: C.darkTextLight }}>{d.volume.toLocaleString()}</td>
                    <td style={{ padding: "12px 10px", fontSize: 13, color: C.darkTextLight }}>{d.avgArea}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
  );
}

/* ================================================================
   CALCULATOR PAGE
   ================================================================ */
function CalculatorPage() {
  const [tab, setTab] = useState("loan");
  const [loanAmt, setLoanAmt] = useState(300000000);
  const [loanRate, setLoanRate] = useState(3.5);
  const [loanYrs, setLoanYrs] = useState(30);
  const [loanType, setLoanType] = useState("equal");
  const [purchPrice, setPurchPrice] = useState(900000000);
  const [firstHome, setFirstHome] = useState(true);
  const [homeCnt, setHomeCnt] = useState(1);
  const [yPrice, setYPrice] = useState(500000000);
  const [mRent, setMRent] = useState(1500000);
  const [jeonse, setJeonse] = useState(300000000);
  const mob = useWindowSize() < 768;

  const fmt = v => { if (v >= 1e8) return `${(v / 1e8).toFixed(1)}`; if (v >= 1e4) return `${Math.round(v / 1e4).toLocaleString()}`; return `${v.toLocaleString()}`; };

  const loanRes = useMemo(() => {
    const P = loanAmt, r = loanRate / 100 / 12, n = loanYrs * 12;
    if (loanType === "equal") { const m = P * r * Math.pow(1 + r, n) / (Math.pow(1 + r, n) - 1); return { monthly: Math.round(m), totalPaid: Math.round(m * n), totalInterest: Math.round(m * n - P) }; }
    const pm = P / n; let ti = 0; for (let i = 0; i < n; i++) ti += (P - pm * i) * r;
    return { monthly: Math.round(pm + P * r), totalPaid: Math.round(P + ti), totalInterest: Math.round(ti) };
  }, [loanAmt, loanRate, loanYrs, loanType]);

  const loanChart = useMemo(() => {
    const d = [], P = loanAmt, r = loanRate / 100 / 12, n = loanYrs * 12; let b = P;
    for (let y = 1; y <= loanYrs; y++) {
      if (loanType === "equal") { const m = P * r * Math.pow(1 + r, n) / (Math.pow(1 + r, n) - 1); for (let i = 0; i < 12; i++) b -= (m - b * r); }
      else { for (let i = 0; i < 12; i++) b -= P / n; }
      d.push({ year: `${y}`, balance: Math.max(0, Math.round(b / 1e4)) });
    }
    return d;
  }, [loanAmt, loanRate, loanYrs, loanType]);

  const taxRes = useMemo(() => {
    let rate; if (homeCnt >= 3) rate = .12; else if (homeCnt >= 2) rate = .08;
    else if (purchPrice <= 6e8) rate = .01; else if (purchPrice <= 9e8) rate = firstHome ? .01 : .02; else rate = firstHome ? .02 : .03;
    const at = Math.round(purchPrice * rate), le = Math.round(at * .1), sr = purchPrice > 6e8 ? Math.round(at * .2) : 0;
    return { rate: (rate * 100).toFixed(1), at, le, sr, total: at + le + sr };
  }, [purchPrice, firstHome, homeCnt]);

  const yieldRes = useMemo(() => {
    const ar = mRent * 12, gi = yPrice - jeonse;
    return { ar, gross: (ar / yPrice * 100).toFixed(2), gi, gap: gi > 0 ? (ar / gi * 100).toFixed(2) : "0" };
  }, [yPrice, mRent, jeonse]);

  const Slider = ({ label, value, onChange, min, max, step, unit, format }) => (
    <div style={{ marginBottom: 20 }}>
      <div style={{ display: "flex", justifyContent: "space-between", marginBottom: 8 }}>
        <label style={{ fontSize: 13, fontWeight: 500, color: C.darkTextLight }}>{label}</label>
        <span style={{ fontSize: 13, fontWeight: 600, color: "#fff", fontFamily: "'Outfit',sans-serif" }}>{format ? format(value) : value}{unit || ""}</span>
      </div>
      <input type="range" min={min} max={max} step={step} value={value} onChange={e => onChange(Number(e.target.value))} />
    </div>
  );

  const tabs = [{ id: "loan", label: " ", icon: PiggyBank }, { id: "tax", label: "", icon: FileText }, { id: "yield", label: "", icon: TrendingUp }];

  return (
    <div style={{ paddingTop: 70, minHeight: "100vh" }}>
      <div style={{ maxWidth: 1200, margin: "0 auto", padding: mob ? "24px 16px" : "40px 24px" }}>
        <div className="ani" style={{ marginBottom: 24 }}>
          <h1 style={{ fontSize: mob ? 22 : 28, fontWeight: 800, marginBottom: 6 }}> </h1>
          <p style={{ color: C.darkText, fontSize: mob ? 13 : 15 }}> , ,  </p>
        </div>

        <div className="ani d1" style={{ display: "flex", gap: 8, marginBottom: 24, overflowX: "auto", WebkitOverflowScrolling: "touch", paddingBottom: 4 }}>
          {tabs.map(t => { const Ic = t.icon, a = tab === t.id; return (
            <button key={t.id} onClick={() => setTab(t.id)} style={{ background: a ? "rgba(0,102,255,.12)" : C.darkCard, border: `1px solid ${a ? C.primary + "40" : C.darkBorder}`, color: a ? C.primary : C.darkTextLight, padding: "10px 16px", borderRadius: 12, fontSize: 13, fontWeight: 600, cursor: "pointer", display: "flex", alignItems: "center", gap: 6, fontFamily: "'Noto Sans KR',sans-serif", whiteSpace: "nowrap", flexShrink: 0 }}><Ic size={15} />{t.label}</button>
          ); })}
        </div>

        {/* LOAN */}
        {tab === "loan" && (
          <div style={{ display: "grid", gridTemplateColumns: mob ? "1fr" : "1fr 1fr", gap: mob ? 12 : 24 }}>
            <div className="ani d2" style={{ background: C.darkCard, border: `1px solid ${C.darkBorder}`, borderRadius: 16, padding: mob ? 20 : 28 }}>
              <h3 style={{ fontSize: mob ? 16 : 18, fontWeight: 700, marginBottom: 20 }}>  </h3>
              <div style={{ display: "flex", gap: 8, marginBottom: 20 }}>
                {["equal","principal"].map(t => (
                  <button key={t} onClick={() => setLoanType(t)} style={{ flex: 1, background: loanType === t ? C.primary : "transparent", border: `1px solid ${loanType === t ? C.primary : C.darkBorder}`, color: loanType === t ? "#fff" : C.darkTextLight, padding: "10px 8px", borderRadius: 10, fontSize: 13, fontWeight: 600, cursor: "pointer", fontFamily: "'Noto Sans KR',sans-serif" }}>{t === "equal" ? "" : ""}</button>
                ))}
              </div>
              <Slider label="" value={loanAmt} onChange={setLoanAmt} min={1e7} max={1e9} step={1e7} format={fmt} />
              <Slider label="" value={loanRate} onChange={setLoanRate} min={1} max={10} step={.1} unit="%" />
              <Slider label="" value={loanYrs} onChange={setLoanYrs} min={5} max={40} step={1} unit="" />
            </div>
            <div className="ani d3" style={{ display: "flex", flexDirection: "column", gap: mob ? 12 : 20 }}>
              <div style={{ background: C.darkCard, border: `1px solid ${C.darkBorder}`, borderRadius: 16, padding: mob ? 20 : 28 }}>
                <h3 style={{ fontSize: mob ? 16 : 18, fontWeight: 700, marginBottom: 16 }}> </h3>
                <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr", gap: 12 }}>
                  <div style={{ background: "rgba(0,102,255,.08)", borderRadius: 12, padding: mob ? 16 : 20, textAlign: "center" }}>
                    <div style={{ fontSize: 11, color: C.darkText, marginBottom: 6 }}> </div>
                    <div style={{ fontSize: mob ? 18 : 22, fontWeight: 700, fontFamily: "'Outfit',sans-serif", color: C.primary }}>{fmt(loanRes.monthly)}</div>
                  </div>
                  <div style={{ background: "rgba(124,92,252,.08)", borderRadius: 12, padding: mob ? 16 : 20, textAlign: "center" }}>
                    <div style={{ fontSize: 11, color: C.darkText, marginBottom: 6 }}> </div>
                    <div style={{ fontSize: mob ? 18 : 22, fontWeight: 700, fontFamily: "'Outfit',sans-serif", color: C.accent1 }}>{fmt(loanRes.totalInterest)}</div>
                  </div>
                </div>
                <div style={{ marginTop: 12, background: "rgba(255,255,255,.03)", borderRadius: 12, padding: 14, display: "flex", justifyContent: "space-between" }}>
                  <span style={{ fontSize: 13, color: C.darkText }}> </span>
                  <span style={{ fontSize: 15, fontWeight: 700, fontFamily: "'Outfit',sans-serif" }}>{fmt(loanRes.totalPaid)}</span>
                </div>
              </div>
              <div style={{ background: C.darkCard, border: `1px solid ${C.darkBorder}`, borderRadius: 16, padding: mob ? "16px 8px" : 24, flex: 1 }}>
                <h4 style={{ fontSize: 13, fontWeight: 600, marginBottom: 12, color: C.darkTextLight, paddingLeft: mob ? 4 : 0 }}> </h4>
                <ResponsiveContainer width="100%" height={mob ? 160 : 180}>
                  <AreaChart data={loanChart}>
                    <defs><linearGradient id="bg2" x1="0" y1="0" x2="0" y2="1"><stop offset="5%" stopColor={C.accent1} stopOpacity={.2} /><stop offset="95%" stopColor={C.accent1} stopOpacity={0} /></linearGradient></defs>
                    <CartesianGrid strokeDasharray="3 3" stroke="rgba(255,255,255,.04)" />
                    <XAxis dataKey="year" tick={{ fill: C.darkText, fontSize: 10 }} axisLine={false} tickLine={false} interval={mob ? 6 : 4} />
                    <YAxis tick={{ fill: C.darkText, fontSize: 10 }} axisLine={false} tickLine={false} tickFormatter={v => `${v}`} width={35} />
                    <Tooltip contentStyle={ttStyle} formatter={v => [`${v.toLocaleString()}`]} />
                    <Area type="monotone" dataKey="balance" stroke={C.accent1} strokeWidth={2} fill="url(#bg2)" dot={false} />
                  </AreaChart>
                </ResponsiveContainer>
              </div>
            </div>
          </div>
        )}

        {/* TAX */}
        {tab === "tax" && (
          <div style={{ display: "grid", gridTemplateColumns: mob ? "1fr" : "1fr 1fr", gap: mob ? 12 : 24 }}>
            <div className="ani d2" style={{ background: C.darkCard, border: `1px solid ${C.darkBorder}`, borderRadius: 16, padding: mob ? 20 : 28 }}>
              <h3 style={{ fontSize: mob ? 16 : 18, fontWeight: 700, marginBottom: 20 }}> </h3>
              <Slider label="" value={purchPrice} onChange={setPurchPrice} min={1e8} max={5e9} step={5e7} format={fmt} />
              <div style={{ marginBottom: 20 }}>
                <label style={{ fontSize: 13, fontWeight: 500, color: C.darkTextLight, marginBottom: 10, display: "block" }}>  </label>
                <div style={{ display: "flex", gap: 8 }}>
                  {[1, 2, 3].map(n => (
                    <button key={n} onClick={() => setHomeCnt(n)} style={{ flex: 1, background: homeCnt === n ? C.primary : "transparent", border: `1px solid ${homeCnt === n ? C.primary : C.darkBorder}`, color: homeCnt === n ? "#fff" : C.darkTextLight, padding: "10px 6px", borderRadius: 10, fontSize: 13, fontWeight: 600, cursor: "pointer", fontFamily: "'Noto Sans KR',sans-serif" }}>{n === 3 ? "3+" : `${n}`}</button>
                  ))}
                </div>
              </div>
              {homeCnt === 1 && (
                <div style={{ display: "flex", gap: 8 }}>
                  {[true, false].map(v => (
                    <button key={String(v)} onClick={() => setFirstHome(v)} style={{ flex: 1, background: firstHome === v ? C.secondary : "transparent", border: `1px solid ${firstHome === v ? C.secondary : C.darkBorder}`, color: firstHome === v ? "#fff" : C.darkTextLight, padding: 10, borderRadius: 10, fontSize: 13, fontWeight: 600, cursor: "pointer", fontFamily: "'Noto Sans KR',sans-serif" }}>{v ? "" : ""}</button>
                  ))}
                </div>
              )}
            </div>
            <div className="ani d3" style={{ background: C.darkCard, border: `1px solid ${C.darkBorder}`, borderRadius: 16, padding: mob ? 20 : 28 }}>
              <h3 style={{ fontSize: mob ? 16 : 18, fontWeight: 700, marginBottom: 20 }}> </h3>
              <div style={{ background: "rgba(0,102,255,.08)", borderRadius: 14, padding: mob ? 20 : 24, textAlign: "center", marginBottom: 20 }}>
                <div style={{ fontSize: 12, color: C.darkText, marginBottom: 6 }}> </div>
                <div style={{ fontSize: mob ? 26 : 32, fontWeight: 800, fontFamily: "'Outfit',sans-serif", color: C.primary }}>{fmt(taxRes.total)}</div>
                <div style={{ fontSize: 12, color: C.darkText, marginTop: 4 }}> {taxRes.rate}%</div>
              </div>
              {[{ l: "", v: taxRes.at }, { l: "", v: taxRes.le }, { l: "", v: taxRes.sr }].map((x, i) => (
                <div key={i} style={{ display: "flex", justifyContent: "space-between", padding: "12px 0", borderBottom: i < 2 ? `1px solid ${C.darkBorder}` : "none" }}>
                  <span style={{ fontSize: 13, color: C.darkText }}>{x.l}</span>
                  <span style={{ fontSize: 14, fontWeight: 600, fontFamily: "'Outfit',sans-serif" }}>{fmt(x.v)}</span>
                </div>
              ))}
            </div>
          </div>
        )}

        {/* YIELD */}
        {tab === "yield" && (
          <div style={{ display: "grid", gridTemplateColumns: mob ? "1fr" : "1fr 1fr", gap: mob ? 12 : 24 }}>
            <div className="ani d2" style={{ background: C.darkCard, border: `1px solid ${C.darkBorder}`, borderRadius: 16, padding: mob ? 20 : 28 }}>
              <h3 style={{ fontSize: mob ? 16 : 18, fontWeight: 700, marginBottom: 20 }}> </h3>
              <Slider label="" value={yPrice} onChange={setYPrice} min={1e8} max={3e9} step={5e7} format={fmt} />
              <Slider label="" value={mRent} onChange={setMRent} min={1e5} max={1e7} step={5e4} format={fmt} />
              <Slider label=" " value={jeonse} onChange={setJeonse} min={0} max={yPrice} step={1e7} format={fmt} />
            </div>
            <div className="ani d3" style={{ display: "flex", flexDirection: "column", gap: mob ? 12 : 20 }}>
              <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr", gap: 12 }}>
                <div style={{ background: C.darkCard, border: `1px solid ${C.darkBorder}`, borderRadius: 16, padding: mob ? 16 : 24, textAlign: "center" }}>
                  <div style={{ fontSize: 12, color: C.darkText, marginBottom: 6 }}> </div>
                  <div style={{ fontSize: mob ? 24 : 28, fontWeight: 800, fontFamily: "'Outfit',sans-serif", color: C.secondary }}>{yieldRes.gross}%</div>
                  <div style={{ fontSize: 11, color: C.darkText, marginTop: 4 }}></div>
                </div>
                <div style={{ background: C.darkCard, border: `1px solid ${C.darkBorder}`, borderRadius: 16, padding: mob ? 16 : 24, textAlign: "center" }}>
                  <div style={{ fontSize: 12, color: C.darkText, marginBottom: 6 }}> </div>
                  <div style={{ fontSize: mob ? 24 : 28, fontWeight: 800, fontFamily: "'Outfit',sans-serif", color: C.accent2 }}>{yieldRes.gap}%</div>
                  <div style={{ fontSize: 11, color: C.darkText, marginTop: 4 }}></div>
                </div>
              </div>
              <div style={{ background: C.darkCard, border: `1px solid ${C.darkBorder}`, borderRadius: 16, padding: mob ? 16 : 24 }}>
                <h4 style={{ fontSize: 13, fontWeight: 600, marginBottom: 14, color: C.darkTextLight }}>  </h4>
                {[{ l: "  ", v: fmt(yieldRes.ar) }, { l: " ", v: fmt(yieldRes.gi) }, { l: "  ", v: `${(yPrice / mRent / 12).toFixed(1)}` }, { l: "", v: `${(jeonse / yPrice * 100).toFixed(1)}%` }].map((x, i) => (
                  <div key={i} style={{ display: "flex", justifyContent: "space-between", padding: "11px 0", borderBottom: i < 3 ? `1px solid ${C.darkBorder}` : "none" }}>
                    <span style={{ fontSize: 13, color: C.darkText }}>{x.l}</span>
                    <span style={{ fontSize: 13, fontWeight: 600, fontFamily: "'Outfit',sans-serif" }}>{x.v}</span>
                  </div>
                ))}
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}

/* ================================================================
   ANALYSIS PAGE
   ================================================================ */
function AnalysisPage() {
  const [selDist, setSelDist] = useState(["", "", ""]);
  const mob = useWindowSize() < 768;

  const toggle = d => {
    if (selDist.includes(d)) { if (selDist.length > 1) setSelDist(selDist.filter(x => x !== d)); }
    else if (selDist.length < 5) setSelDist([...selDist, d]);
  };

  const compData = useMemo(() => {
    const arr = [];
    for (let y = 2023; y <= 2025; y++)
      for (let m = 1; m <= 12; m++) {
        const e = { date: `${y}.${String(m).padStart(2, "0")}` };
        selDist.forEach(d => {
          const base = BASE_PRICE[d] || 10;
          e[d] = Math.round((base + (y - 2023) * .8 + Math.sin((m + y * 12) * .3) * 1.5 + (Math.random() - .5)) * 100) / 100;
        });
        arr.push(e);
      }
    return arr;
  }, [selDist]);

  const pieData = DISTRICT_SUMMARY.slice(0, 8).map(d => ({ name: d.name, value: d.volume }));

  return (
    <div style={{ paddingTop: 70, minHeight: "100vh" }}>
      <div style={{ maxWidth: 1200, margin: "0 auto", padding: mob ? "24px 16px" : "40px 24px" }}>
        <div className="ani" style={{ marginBottom: 24 }}>
          <h1 style={{ fontSize: mob ? 22 : 28, fontWeight: 800, marginBottom: 6 }}> </h1>
          <p style={{ color: C.darkText, fontSize: mob ? 13 : 15 }}>     </p>
        </div>

        <div className="ani d1" style={{ background: C.darkCard, border: `1px solid ${C.darkBorder}`, borderRadius: 16, padding: mob ? 16 : 24, marginBottom: 20 }}>
          <h3 style={{ fontSize: 13, fontWeight: 600, color: C.darkTextLight, marginBottom: 12 }}>   ( 5)</h3>
          <div style={{ display: "flex", gap: 6, flexWrap: "wrap" }}>
            {DISTRICTS.map(d => {
              const a = selDist.includes(d);
              return <button key={d} onClick={() => toggle(d)} style={{ background: a ? "rgba(0,102,255,.15)" : "rgba(255,255,255,.04)", border: `1px solid ${a ? C.primary + "50" : C.darkBorder}`, color: a ? C.primary : C.darkTextLight, padding: "6px 12px", borderRadius: 8, fontSize: 12, fontWeight: 500, cursor: "pointer", fontFamily: "'Noto Sans KR',sans-serif" }}>{d}</button>;
            })}
          </div>
        </div>

        <div className="ani d2" style={{ background: C.darkCard, border: `1px solid ${C.darkBorder}`, borderRadius: 16, padding: mob ? "16px 6px" : 24, marginBottom: 20 }}>
          <h3 style={{ fontSize: mob ? 14 : 16, fontWeight: 700, marginBottom: 16, paddingLeft: mob ? 10 : 0 }}>  </h3>
          <ResponsiveContainer width="100%" height={mob ? 280 : 380}>
            <LineChart data={compData}>
              <CartesianGrid strokeDasharray="3 3" stroke="rgba(255,255,255,.04)" />
              <XAxis dataKey="date" tick={{ fill: C.darkText, fontSize: 10 }} axisLine={false} tickLine={false} interval={mob ? 7 : 5} />
              <YAxis tick={{ fill: C.darkText, fontSize: 10 }} axisLine={false} tickLine={false} tickFormatter={v => `${v}`} width={35} />
              <Tooltip contentStyle={ttStyle} formatter={v => [`${v}`]} />
              {selDist.map((d, i) => <Line key={d} type="monotone" dataKey={d} stroke={CC[i % CC.length]} strokeWidth={2} dot={false} />)}
            </LineChart>
          </ResponsiveContainer>
          <div style={{ display: "flex", gap: mob ? 12 : 20, marginTop: 12, justifyContent: "center", flexWrap: "wrap" }}>
            {selDist.map((d, i) => (
              <div key={d} style={{ display: "flex", alignItems: "center", gap: 5, fontSize: 12, color: C.darkTextLight }}>
                <div style={{ width: 10, height: 3, borderRadius: 2, background: CC[i % CC.length] }} />{d}
              </div>
            ))}
          </div>
        </div>

        <div style={{ display: "grid", gridTemplateColumns: mob ? "1fr" : "1fr 1fr", gap: mob ? 12 : 24 }}>
          <div className="ani d3" style={{ background: C.darkCard, border: `1px solid ${C.darkBorder}`, borderRadius: 16, padding: mob ? "16px 6px" : 24 }}>
            <h3 style={{ fontSize: mob ? 14 : 16, fontWeight: 700, marginBottom: 16, paddingLeft: mob ? 10 : 0 }}>  </h3>
            <ResponsiveContainer width="100%" height={mob ? 240 : 280}>
              <PieChart><Pie data={pieData} cx="50%" cy="50%" innerRadius={mob ? 55 : 70} outerRadius={mob ? 90 : 110} paddingAngle={3} dataKey="value">
                {pieData.map((_, i) => <Cell key={i} fill={CC[i % CC.length]} />)}
              </Pie><Tooltip contentStyle={ttStyle} formatter={v => [`${v.toLocaleString()}`]} /></PieChart>
            </ResponsiveContainer>
            <div style={{ display: "flex", gap: 10, flexWrap: "wrap", justifyContent: "center" }}>
              {pieData.map((d, i) => <div key={i} style={{ display: "flex", alignItems: "center", gap: 4, fontSize: 11, color: C.darkTextLight }}><div style={{ width: 8, height: 8, borderRadius: 2, background: CC[i % CC.length] }} />{d.name}</div>)}
            </div>
          </div>
          <div className="ani d4" style={{ background: C.darkCard, border: `1px solid ${C.darkBorder}`, borderRadius: 16, padding: mob ? 16 : 24 }}>
            <h3 style={{ fontSize: mob ? 14 : 16, fontWeight: 700, marginBottom: 16 }}>  </h3>
            <table style={{ width: "100%", borderCollapse: "collapse" }}>
              <thead><tr>{["","","",""].map(h => <th key={h} style={{ padding: "10px 10px", textAlign: "left", fontSize: 11, fontWeight: 600, color: C.darkText, borderBottom: `1px solid ${C.darkBorder}` }}>{h}</th>)}</tr></thead>
              <tbody>{selDist.map(d => { const inf = DISTRICT_SUMMARY.find(x => x.name === d); return (
                <tr key={d}>
                  <td style={{ padding: "12px 10px", fontSize: 13, fontWeight: 600 }}>{d}</td>
                  <td style={{ padding: "12px 10px", fontSize: 13, fontFamily: "'Outfit',sans-serif" }}>{inf?.avgPrice}</td>
                  <td style={{ padding: "12px 10px", fontSize: 12, fontWeight: 600, color: inf?.change >= 0 ? C.danger : C.secondary }}>{inf?.change >= 0 ? "+" : ""}{inf?.change}%</td>
                  <td style={{ padding: "12px 10px", fontSize: 13, color: C.darkTextLight }}>{inf?.volume.toLocaleString()}</td>
                </tr>
              ); })}</tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
  );
}

/* ================================================================
   FOOTER
   ================================================================ */
function Footer() {
  const mob = useWindowSize() < 768;
  return (
    <footer style={{ borderTop: `1px solid ${C.darkBorder}`, padding: mob ? "28px 16px" : "40px 24px", marginTop: 40 }}>
      <div style={{ maxWidth: 1200, margin: "0 auto", display: "flex", justifyContent: "space-between", alignItems: "center", flexWrap: "wrap", gap: 16, flexDirection: mob ? "column" : "row" }}>
        <div style={{ display: "flex", alignItems: "center", gap: 8 }}>
          <div style={{ width: 24, height: 24, borderRadius: 6, background: C.gradient1, display: "flex", alignItems: "center", justifyContent: "center" }}><Building2 size={12} color="white" /></div>
          <span style={{ fontFamily: "'Outfit',sans-serif", fontWeight: 600, fontSize: 14 }}><span style={{ color: C.primary }}>Valley</span></span>
        </div>
        <div style={{ fontSize: 12, color: C.darkText, textAlign: mob ? "center" : "right" }}>    2025 Valley</div>
      </div>
    </footer>
  );
}

/* ================================================================
   APP  entry point
   ================================================================ */
export default function App() {
  const [authed, setAuthed] = useState(false);
  const [page, setPage] = useState("home");
  const [checking, setChecking] = useState(true);

  useEffect(() => {
    try { if (sessionStorage.getItem(STORAGE_KEY) === "true") setAuthed(true); } catch (_) {}
    setChecking(false);
  }, []);

  useEffect(() => { window.scrollTo({ top: 0, behavior: "smooth" }); }, [page]);

  const logout = () => {
    try { sessionStorage.removeItem(STORAGE_KEY); } catch (_) {}
    setAuthed(false); setPage("home");
  };

  if (checking) return null;
  if (!authed) return (<><style>{globalStyles}</style><LoginPage onAuth={() => setAuthed(true)} /></>);

  return (
    <>
      <style>{globalStyles}</style>
      <Nav currentPage={page} setCurrentPage={setPage} onLogout={logout} />
      {page === "home" && <LandingPage setCurrentPage={setPage} />}
      {page === "dashboard" && <DashboardPage />}
      {page === "calculator" && <CalculatorPage />}
      {page === "analysis" && <AnalysisPage />}
      <Footer />
    </>
  );
}
